*,*::before,*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Poppins', 'sans-serif';
}

.wrapper{
  width: 100vw;
  height: 100vh;
  background-image: url(./asset/gradient-bg.jpg);
  background-size: cover;
  background-position: center;
  display: grid;
  place-items: center;
}

.game-info{
  color:white;
  position:absolute;
  top:3rem;
  left:50%;
  transform:translateX(-50%);
  background-color: rgba(255, 255, 255, 0.15);
  border-radius: 1rem;
  border: 1px solid rgba(255, 255, 255, 0.25);
  padding: 0.5rem 3rem;
  /* text-align:center; */

}

.tic-tac-toe{
  width:90%;
  max-width:20rem;
  background-color: rgba(255, 255, 255, 0.15);
  border-radius: 1rem;
  border: 1px solid rgba(255, 255, 255, 0.25);
  padding: 2rem;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  aspect-ratio: 1/1;
}


.box{
  position:relative;
  width:100%;
  aspect-ratio: 1/1;
  cursor: pointer;
  font-size: 3rem;
  color:white;
  display: flex;
  justify-content: center;
  align-items: center;
}

.box0, .box1, .box3, .box4{
  border-right:2px solid white;
  border-bottom:2px solid white;
}

.box2, .box5{
  border-bottom: 2px solid white;
}

.box6, .box7{
  border-right:2px solid white;
}

.btn{
  color:white;
  position: absolute;
  bottom:3rem;
  left:50%;
  transform:translateX(-50%);
  background-color: rgba(255, 255, 255, 0.15);
  border-radius: 1rem;
  border: 1px solid rgba(255, 255, 255, 0.25);
  padding: 0.5rem 2rem;
  cursor:pointer;
  display: none;
}

.btn.active{
  display: flex;
}

.win{
  background-color: rgba(0, 255, 0, 0.3);
}